.root {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  display: block;
  width: 20px;
  height: 20px;
}

.small {
  width: 18px;
  height: 18px;
}

.clickable {
  cursor: pointer;
}

/* MENU ITEMS: */

.Categories {
  background-image: url(svg/tag.svg);
}

.Home {
  background-image: url(svg/home.svg);
}

.Percentage {
  background-image: url(svg/chart-pie.svg);
}

.Rules {
  background-image: url(svg/code-branch.svg);
}

.Transactions {
  background-image: url(svg/exchange-alt.svg);
}

.Trend {
  background-image: url(svg/chart-area.svg);
}

/* NOTIFICATION SEVERITY: */
.info {
  background-image: url(svg/info-circle.svg);
}

.debug {
  background-image: url(svg/bug.svg);
}

.warning {
  background-image: url(svg/exclamation-triangle.svg);
}

.error {
  background-image: url(svg/times-circle.svg);
}

/* MISC: */

.menu {
  background-image: url(svg/bars.svg);
}

.maskOff {
  background-image: url(svg/mask-gray.svg);
}

.maskOn {
  background-image: url(svg/mask-orange.svg);
}

.networkOff {
  background-image: url(svg/network-wired-gray.svg);
}

.networkOn {
  background-image: url(svg/network-wired-orange.svg);
}

.next {
  background-image: url(svg/angle-right.svg);
}

.no {
  background-image: url(svg/times.svg);
}

.previous {
  background-image: url(svg/angle-left.svg);
}

.question {
  background-image: url(svg/question-circle.svg);
}

.save {
  background-image: url(svg/save.svg);
}

.yes {
  background-image: url(svg/check.svg);
}
