.root {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  /* overflow: auto; /* Enable scroll if needed */
  background-color: rgba(0, 0, 0, 0.2);

  display: flex;
  align-items: center;
  justify-content: center;
}

.box {
  /* position: fixed; */
  /* top: 30%; */
  /* left: 30%; */
  min-width: 500px;
  background-color: white;
  border-radius: 10px;
  border: 1px solid #aaaaaa;
}

.header,
.footer {
  width: 100%;
  background-color: #f0f0f0;
  padding: 15px 20px;
}

.header {
  border-radius: 10px 10px 0 0;
  font-weight: bold;
  font-size: 20px;
}

.footer {
  border-radius: 0 0 10px 10px;
  display: flex;
  justify-content: space-between;
}

.component {
  padding: 40px 20px;
}
