.root {
  position: fixed;
  z-index: 2;
  background-color: white;
  top: 11px;
  left: 4px;
  border: 1px solid gainsboro;
  border-radius: 3px;
  padding: 10px 20px 10px 10px;
  box-shadow: 0 -6px 0 #fff, 0 1px 6px rgba(0, 0, 0, 0.35);

  display: flex;
  flex-direction: column;
}

.root nav {
  margin-top: 20px;
}
