/* FONTS: */

/* https://stackoverflow.com/questions/41676054/how-to-add-fonts-to-create-react-app-based-projects */
/* See: 3. Downloading the font and adding it in your source code */
@font-face {
  font-family: "Corbel";
  src: local("Corbel"), url(./fonts/corbel.ttf) format("truetype");
}

/* HTML ELEMENTS */

* {
  box-sizing: border-box;
}

/* Disable horizontal scrolling in html for the iphone */
/* https://stackoverflow.com/questions/45924217/how-to-disable-horizontal-scrolling-in-html-for-the-iphone/45924335 */
html,
body {
  max-width: 100% !important;
  overflow-x: hidden !important;
}

body {
  margin: 0;
  color: #666666;
}

body,
input,
button,
select {
  font-family: Corbel;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 18px;
}
@media screen and (max-width: 480px) {
  body,
  input,
  button,
  select {
    font-size: 14px;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

table {
  width: 100%;
  border-collapse: collapse;
}

table thead tr th,
table tbody tr td {
  border: 1px solid gainsboro;
  padding: 0 3px 2px 3px;
}

table thead tr th {
  font-weight: bold;
}

button {
  box-shadow: 3px 3px 4px rgba(0, 0, 0, 0.3);
  background-color: gainsboro;
  border: 1px solid gainsboro;
  border-radius: 5px;
  padding: 3px 7px;
  cursor: pointer;
  opacity: 0.8;
}

button:hover {
  background-color: #bbbbbb;
  border: 1px solid #bbbbbb;
}

input {
  border: 1px solid gainsboro;
  padding: 3px 5px;
}

select {
  border: 1px solid gainsboro;
  width: 100%;
  color: #666666;
  min-width: 70px;
}

/* CLASSES: */

.page {
  padding: 0 20px 20px 20px;
}
@media screen and (max-width: 480px) {
  .page {
    padding: 0 7px 20px 7px;
  }
}

.pageBoxes {
  display: flex;
  justify-content: space-between;
}

.pageBoxes > div {
  background-color: #fefefe;
  border: 1px solid gainsboro;
  border-radius: 5px;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.25);
}

.pageBoxes * {
  cursor: pointer !important;
}

.flexRow {
  display: flex;
  align-items: center;
}

.flexRowLeft {
  display: flex;
  align-items: center;
}

.flexRowWithMargin {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.flexRowWithMarginBaseline {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
}

.flexRow > *,
.flexRowWithMargin > *,
.flexRowWithMarginBaseline > * {
  margin-right: 20px;
}

.flexRowLeft > * {
  margin-right: 7px;
}

.flexRow > *:last-child,
.flexRowLeft > *:last-child,
.flexRowWithMargin > *:last-child,
.flexRowWithMarginBaseline > *:last-child {
  margin-right: 0;
}

.flexColumn {
  flex-direction: column;
  /* align-items: flex-start; */
}

.textAlignRight {
  text-align: right;
}
