.root {
  /* justify-content: flex-start !important; */
  padding: 15px;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.25);
}

.stickyTop {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: white;
  z-index: 1;
}

.stickyBottom {
  height: 60px; /* measure the pageHeader height on the browser */
}

.appTitle {
  color: sandybrown;
  font-size: 24px;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.logo {
  width: 24px;
  height: 24px;
}
