.root {
  background-color: white;
  position: relative;
}

.topBar {
  height: 24px;
  width: 100%;
  border: 1px solid gainsboro;
  line-height: 24px;
  display: flex;
  justify-content: space-between;
  cursor: default;
}

.topBarText {
  padding: 0 4px;
}

.topBarButton {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  display: block;
  width: 17px;
  height: 24px;
  background-image: url(downArrowButton.png);
}

.items {
  border: 1px solid #a0a0a0;
  padding: 1px 1px 1px 1px;
  position: absolute;
  background-color: white;
  width: 96%;
  /* box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.3); */
  /* box-shadow: 0 -6px 0 #fff, 0 1px 6px rgba(0, 0, 0, 0.35); */
}

.item {
  display: flex;
  margin: 1px 0;
  padding: 1px 6px;
  border-radius: 3px;
  cursor: default;
}

.item:hover {
  opacity: 0.7;
}

.selectMarker {
  width: 4px;
  margin-right: 3px;
}

.selected {
  background-color: sandybrown;
}

.item:hover .selectMarker {
  background-color: gainsboro;
}
